export const itemsAdd = items => ({
	type: "JOBS_ADD",
	items: items.map ? items : []
});
export const itemStatus = (_id, _status) => ({
	type: "JOB_STATUS",
	_id,
	_status
});

export const filterAdd = filter => ({
	type: "FILTER_ADD",
	filter
});
export const filterRemove = filter => ({
	type: "FILTER_REMOVE",
	filter
});

// export const areaSelect = area_route => ({
// 	type: "AREA_SELECT",
// 	area_route
// });
export const new_items__add = (items, area_key) => ({
	type: "new_items__add",
	items,
	area_key
});



